import React, { Component } from 'react';
import MediaCapturer from 'react-multimedia-capture';


class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {login : '', password : ''}

        this.handleLoginChange = this.changeLogin.bind(this);
        this.handlePasswordChange = this.changePassword.bind(this);
    }

    changeLogin(event) {
        this.setState({ login : event.target.value })
    }

    changePassword(event) {
        this.setState({ password : event.target.value })
    }

    render () {
        return (
            <form>
                <h2>Login</h2>
                Login <input type="text" value={this.state.login} onChange={this.handleLoginChange}/> <br/>
                Password <input type="password" value={this.state.password} onChange={this.handlePasswordChange}/> <br/>
                <input type="button" value="submit" onClick={this.props.login.bind(this.props.parent, this.state.login, this.state.password)}/>
            </form>
        )
    }
}

class Recording extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			granted: false,
			rejectedReason: '',
			recording: false,
			paused: false
		};

		this.handleStart = this.handleStart.bind(this);
		this.handleStop = this.handleStop.bind(this);
		this.handleStreamClose = this.handleStreamClose.bind(this);
		//this.downloadAudio = this.downloadAudio.bind(this);
    }
    
    authenticated(token) {
        this.setState({ token : token, errormessage : undefined })
    }

    failedauthenticated() {
        this.setState({ token : undefined, errormessage : 'Authentication Error' })
    }
	
	handleStart(stream) {
		this.setState({
			recording: true
		});

		console.log('Recording Started.');
	}
	handleStop(blob) {
        //blob tem o audio
        // fazer o post aqui
		this.setState({
			recording: false
		});

        console.log('Recording Stopped.');
        console.log(blob);
        //this.downloadAudio(blob);
        
        //post e fetch da info na lambda func do API Gateway AWS

		console.log("auth token in recorder --> " + this.props.token );
        
        //pack recording to POST
        
        let send_rec = new FileReader();
        send_rec.readAsDataURL(blob);
        send_rec.onloadend = () => {
            var dataUrl = send_rec.result;
            var base64data = dataUrl.split(',')[1];
            console.log("datUrl " + dataUrl);
            console.log("ENCODED ---- " + base64data);   
         
            
            //fetch

            var theobject = this
            var formparameters = {
                method: 'POST', // or 'PUT'
                body: JSON.stringify({'audio_encoded' : base64data}),
                headers:{
                'Content-Type': 'application/json',
                'authorizationToken' : JSON.stringify({ 'token' : this.props.token })
                }
            }
            
            console.log(formparameters);
            console.log('trancriber server ->  ' + this.props.transcriberserver);

            fetch(this.props.transcriberserver, formparameters).then(function(data) {
                
                        if(data.status!==200) {
                            theobject.failedauthenticated()
                            throw new Error(data.status)
                        }
                        else {
                            var json = data.json();
                            return json;
                        }
            }).then(function(thetoken) {
                        console.log('message =', thetoken)
                        if ('token' in thetoken)
                            theobject.authenticated(thetoken['token'])
            }).catch(function(error) {
                        console.log('There has been a problem with your fetch operation: ', error.message);
                    });

        }
	}
	
	handleStreamClose() {
		this.setState({
			granted: false
		});
	}
	handleError(err) {
		console.log(err);
	}
	/*downloadAudio(blob) {
		let url = URL.createObjectURL(blob);
		let a = document.createElement('a');
		a.style.display = 'none';
		a.href = url;
		a.target = '_blank';
		document.body.appendChild(a);

		a.click();
	}*/
	render() {
		const recording = this.state.recording;
        
		return (
			<div ref="app">
				<h3>Recording your Destiny</h3>
				<MediaCapturer
					constraints={{ audio: true }}
					mimeType="audio/webm"
					timeSlice={10}
					onStart={this.handleStart}
                    onStop={this.handleStop}
					onError={this.handleError} 
					onStreamClosed={this.handleStreamClose}
					render={({ start, stop }) => 
					<div>
						<p>Recording: {recording.toString()}</p>
						<button onClick={start}>Start</button>
						<button onClick={stop}>Stop</button>
					</div>
				}/>
			</div>
		);
	}
}


class Page extends Component {
    constructor(props) {
        super(props);
        this.state = {token : undefined, errormessage : undefined };
    }

    authenticated(token) {
        this.setState({ token : token, errormessage : undefined })
    }

    failedauthenticated() {
        this.setState({ token : undefined, errormessage : 'Authentication Error' })
    }

    doLogin(login, password) {
        var theobject = this
        var formparameters = {
            method: 'POST', // or 'PUT'
            body: JSON.stringify({'login' : login, 'password' : password}),
            headers:{
              'Content-Type': 'application/json',
            }
        }
        console.log(formparameters);
        console.log("loginserver --> " + this.props.loginserver);
  fetch(this.props.loginserver, formparameters).then(function(data) {
            if(data.status!==200) {
                theobject.failedauthenticated()
                throw new Error(data.status)
            }
            else {
                var json = data.json();
                return json;
            }
  }).then(function(thetoken) {
            console.log('message =', thetoken)
            if ('token' in thetoken)
                theobject.authenticated(thetoken['token'])
  }).catch(function(error) {
            console.log('There has been a problem with your fetch operation: ', error.message);
        });
    }

    render() {
        if (this.state.errormessage != undefined)
            var errormessage = <h2>{this.state.errormessage}</h2>
        else
            var errormessage = <div/>
        if (this.state.token == undefined)
            return (
                <div>
                    {errormessage}
                    <Login parent={this} login={this.doLogin}/>
                </div>
            )
        else
            return (
                <div>
                    {errormessage}
                    <Recording transcriberserver = {this.props.transcriberserver} token = {this.state.token}/>
                </div>
            )
    }
}

